      isShow: true,
    };
  },

  mounted() {
    this.$progress.start(); // 进度条加载
    this.$ProjectModels.getUserInfoModels().then((res) => {
      this.userInfo.username = res.data.username;
      this.userInfo.email = res.data.email;
      this.userInfo.city = res.data.city;
      this.userInfo.country = res.data.country;
      this.userInfo.telephone = res.data.telephone;
      this.$progress.finish(); // 进度条完成
    });
  },

  methods: {
    handleShow() {
      this.isShow = false;
    },
  },
};
</script>

